/* ------------------------------------------------------
Site Utility
--------------------------------------------------------- */
@font-face {
	font-family: 'icomoon';
	src: url("fonts/icomoon.eot?531r0y");
	src: url("fonts/icomoon.eot?531r0y#iefix") format("embedded-opentype"), url("fonts/icomoon.ttf?531r0y") format("truetype"), url("fonts/icomoon.woff?531r0y") format("woff"), url("fonts/icomoon.svg?531r0y#icomoon") format("svg");
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	speak: never;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.icon-shopping-cart:before {
	content: "\e902";
}

.icon-heart-o:before {
	content: "\e900";
}

.icon-user-circle-o:before {
	content: "\e901";
}

/* ------------------------------------------------------
Site Header section
--------------------------------------------------------- */
.single-product #site-header {
	border-bottom: 0;
}

#site-header {
	border-bottom: 1px solid rgba(0, 0, 0, 0.12);
	float: none;
}

#site-header * {
	float: none;
}

#site-header .container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 1.875rem 0.9375rem 20px;
}

#site-header::before, #site-header::after {
	display: none;
}

#logo {
	font-size: 2.25rem;
	font-weight: 500;
	line-height: 1;
	text-transform: capitalize;
}

#logo a {
	color: #20303c;
}

#logo a:focus, #logo a:hover {
	color: #96588a;
}

/* ------------------------------------------------------
Components
--------------------------------------------------------- */
#primary-navigation .navigation .menu {
	display: flex;
}

#shop-navigation .shop-menu {
	display: flex;
}

#shop-navigation .shop-menu li {
	font-size: 1rem;
	margin-right: 15px;
}

#shop-navigation .shop-menu li:last-child {
	margin-right: 0;
}

#shop-navigation a {
	color: #20303c;
}

#shop-navigation a:focus, #shop-navigation a:hover {
	color: #96588a;
}

#shop-navigation .wpmenucart-contents {
	font-weight: bold;
	color: #e35640;
}

.woo-icon {
	font-size: 0;
}

.woo-icon .fa {
	font-size: 1.25rem;
}

.custom-breadcrumb {
	background-color: #f2f2f2;
	padding-top: 0.9375rem;
	padding-bottom: 0.9375rem;
}

.woocommerce .woocommerce-breadcrumb {
	font-size: 0.75em !important;
	margin-bottom: 0 !important;
}

input,
.button,
.onsale {
	border-radius: 0 !important;
}

/* ------------------------------------------------------
Woocommerce styles
--------------------------------------------------------- */
.pagination a,
.pagination .page-numbers.dots,
.woocommerce nav.woocommerce-pagination ul li a {
	font-size: 1.25rem;
}

.woocommerce nav.woocommerce-pagination ul li span.current,
.woocommerce-page nav.woocommerce-pagination ul li span.current,
.woocommerce #content nav.woocommerce-pagination ul li span.current,
.woocommerce-page #content nav.woocommerce-pagination ul li span.current {
	font-size: 1.25rem !important;
}

.woocommerce-account .woocommerce-MyAccount-navigation {
	margin-bottom: 2.5rem;
}

.woocommerce-account .woocommerce-MyAccount-content {
	width: 100% !important;
}

@media (min-width: 600px) {
	.woocommerce-account .woocommerce-MyAccount-content {
		width: 70% !important;
	}
}

.post-type-archive-product article {
	width: 100% !important;
}

.post-type-archive-product .sidebar {
	width: 100% !important;
	float: left !important;
}

@media (min-width: 900px) {
	.post-type-archive-product article {
		width: 73% !important;
	}
	.post-type-archive-product .sidebar {
		width: 20% !important;
		float: right !important;
	}
}

.single-product #page article {
	width: 100%;
}

.single-product #page .sidebar {
	width: 100%;
	float: left;
}

@media (min-width: 900px) {
	.single-product #page article {
		width: 73%;
	}
	.single-product #page .sidebar {
		width: 20%;
		float: right;
	}
}

.single-product .product_meta {
	font-size: 1rem;
	border-top: 1px solid #ccc;
	margin-top: 1.875rem;
	padding-top: 0.625rem;
}

.single-product .product_meta span {
	display: block;
	padding: 5px 0;
}

.single-product .product_meta span span {
	display: inline;
}

.show-title-form {
	background-color: transparent !important;
	color: #20303c;
}

.woocommerce-product-gallery .flex-control-thumbs li {
	margin-bottom: 0.3125rem !important;
}

.woocommerce-cart .hero {
	background-color: #f2f2f2;
	padding: 1.25rem 0;
	text-align: center;
}

.woocommerce-cart .hero .entry-title {
	margin: 0;
}

.woocommerce-cart .woocommerce {
	display: flex;
	flex-flow: column;
}

@media (min-width: 1100px) {
	.woocommerce-cart .woocommerce {
		flex-flow: row wrap;
		justify-content: space-between;
	}
}

.woocommerce-cart .woocommerce .woocommerce-notices-wrapper {
	width: 100%;
}

.woocommerce-cart .woocommerce .woocommerce-cart-form {
	width: 100%;
}

@media (min-width: 1100px) {
	.woocommerce-cart .woocommerce .woocommerce-cart-form {
		width: calc(100% - 450px);
	}
}

.woocommerce-cart .woocommerce .woocommerce-cart-form .shop_table {
	border: 0;
}

.woocommerce-cart .woocommerce .woocommerce-cart-form .shop_table th {
	background-color: #e8e8e8;
}

.woocommerce-cart .woocommerce .woocommerce-cart-form .shop_table td {
	border: 0;
	background-color: #fbfbfb;
}

.woocommerce-cart .woocommerce .woocommerce-cart-form .shop_table td.actions {
	background-color: white;
	border-top: 1px solid #20303c;
}

.woocommerce-cart .woocommerce .cart-collaterals {
	width: 100%;
}

@media (min-width: 1100px) {
	.woocommerce-cart .woocommerce .cart-collaterals {
		width: 26.25rem;
	}
}

.woocommerce-cart .woocommerce .cart-collaterals .cart_totals {
	width: 100%;
}

.woocommerce-cart .woocommerce .cart-collaterals .cart_totals h2 {
	font-size: 0;
	color: transparent;
	margin: 0;
}

.woocommerce-cart .woocommerce .cart-collaterals .cart_totals .shop_table {
	border: 0;
}

.woocommerce-cart .woocommerce .cart-collaterals .cart_totals .shop_table th {
	font-weight: 400;
	border: 0;
	background-color: #e8e8e8;
}

.woocommerce-cart .woocommerce .cart-collaterals .cart_totals .shop_table td {
	border: 0;
	background-color: #f0f1f2;
}

.woocommerce-cart .woocommerce .cart-collaterals .cart_totals .shop_table .shipping_method {
	margin-bottom: 1.25rem;
}

.woocommerce-cart .woocommerce .cart-collaterals .cart_totals .woocommerce-shipping-destination {
	border-top: 1px solid #20303c;
	padding-top: 0.5625rem;
}

.woocommerce-checkout {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

#order_review,
#customer_details {
	width: 48%;
}

#order_review h3 {
	margin-bottom: 1.875rem;
}

/* ------------------------------------------------------
Homepage Page Template Styles
--------------------------------------------------------- */
.page-template-page-home #page {
	padding-top: 2.5rem;
}

.page-template-page-home article header {
	float: none;
}

.page-template-page-home article header .entry-title {
	font-size: 0;
	color: transparent;
	margin: 0;
}

.home .page h1.title {
	font-size: 0;
	color: transparent;
	margin: 0;
}

/* ------------------------------------------------------
Homepage Grid
--------------------------------------------------------- */
.home-grid .wc-block-grid__product-link {
	display: block;
	position: relative;
}

.home-grid .wc-block-grid__product-link::before, .home-grid .wc-block-grid__product-link::after {
	content: '';
	position: absolute;
	top: 1.5rem;
	right: 1.5rem;
	bottom: 1.5rem;
	left: 1.5rem;
	transition: 0.5s;
	z-index: 1;
}

.home-grid .wc-block-grid__product-link::before {
	border-top: 1px solid white;
	border-bottom: 1px solid white;
	transform: scale(0, 1);
}

.home-grid .wc-block-grid__product-link::after {
	border-right: 1px solid white;
	border-left: 1px solid white;
	transform: scale(1, 0);
}

.home-grid .wc-block-grid__product-link .wc-block-grid__product-image {
	background-color: #96588a;
	margin-bottom: 0;
}

.home-grid .wc-block-grid__product-link .wc-block-grid__product-image:nth-child(odd) {
	background-color: #069;
}

.home-grid .wc-block-grid__product-link .wc-block-grid__product-image img {
	display: block;
	opacity: 1;
	transition: 0.5s;
}

.home-grid .wc-block-grid__product-link .wc-block-grid__product-title {
	font-size: 1.375rem;
	font-weight: normal;
	color: white;
	margin-bottom: 0;
	padding: 20px;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: flex;
	flex-flow: column;
	justify-content: center;
	opacity: 0;
	transition: 0.5s;
}

.home-grid .wc-block-grid__product-link:focus::before, .home-grid .wc-block-grid__product-link:focus::after, .home-grid .wc-block-grid__product-link:hover::before, .home-grid .wc-block-grid__product-link:hover::after {
	transform: scale(1);
}

.home-grid .wc-block-grid__product-link:focus img, .home-grid .wc-block-grid__product-link:hover img {
	opacity: 0.2;
}

.home-grid .wc-block-grid__product-link:focus .wc-block-grid__product-title, .home-grid .wc-block-grid__product-link:hover .wc-block-grid__product-title {
	opacity: 1;
}

/* ------------------------------------------------------
Site Footer section
--------------------------------------------------------- */
#site-footer {
	background-color: #f0f1f2 !important;
	border-top: 0;
}

#move-to-top {
	display: none;
}
